.nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  height: 6.25em;
}

.site-header {
  padding-top: 0 !important;
}

.footer-box {
  display: flex;
  justify-content: center;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  font-weight: 600;
  padding: 0.9375em 1.875em;
  border-radius: 0.9375em;
  border: none;
  text-transform: uppercase;
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  will-change: transform;
  box-shadow: 0.125em 0.375em 0.625em rgba(51, 51, 51, 0.15);
}
.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0.125em 0.75em 0.625em rgba(51, 51, 51, 0.15);
}
.btn--color {
  background-color: #01c3ff;
  color: #fff;
}
.btn:active {
  transform: scale(0.97);
}
.btn:focus {
  outline: transparent;
}

*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
}

body {
  box-sizing: border-box;
}

.container {
  max-width: 73.75em;
  margin: 0 auto;
}